* {
  box-sizing: border-box; }

html {
  font-size: 62.5%; }

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: greyscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.popup {
  min-height: 5rem; }

.row {
  display: flex;
  justify-content: center; }

#data-container {
  background-image: linear-gradient(#777777, #666666, #444444, #444444, #333333);
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-content: center;
  border: 0.2rem solid #666666;
  border-radius: .5rem; }

.box > div:first-child {
  -webkit-text-stroke: 0.5px #000000; }

.box > div:last-child {
  width: 100%;
  background-color: rgba(235, 235, 235, 0.8);
  border: 1px solid #bebebe;
  border-radius: .3rem;
  padding: .5rem 1rem;
  color: ghostwhite;
  box-shadow: inset 0 0.2rem 0 #00afaf, 0 0.2rem 0.5rem #000000;
  text-align: center;
  transition: all 1.5s ease-in; }
  .box > div:last-child:hover {
    background-color: #00afaf;
    box-shadow: inset 0 0.2rem 0 #006060, 0 0.2rem 0.5rem #000000; }

.nav-container {
  width: 100%;
  padding: 20px 0;
  text-align: center; }
  .nav-container a {
    font-size: 18px;
    text-decoration: none;
    color: #333333;
    margin: 10px;
    padding-bottom: 3px; }
  .nav-container a:hover {
    border-bottom: 2px solid #333333; }

.linechart {
  padding: .8rem; }
  .linechart__path {
    stroke-width: 3;
    fill: none; }
  .linechart__axis {
    stroke: red; }
  .linechart__point {
    fill: #ffffff;
    stroke-width: 2; }
  .linechart__area {
    padding: .8rem;
    fill: #00ff51;
    stroke: none;
    opacity: .4; }
  .linechart__label {
    fill: #000000;
    font-weight: 700; }

.hoverline {
  stroke: #cccccc;
  stroke-width: 1; }

.hover {
  display: flex;
  flex-direction: column;
  align-items: center;
  position: absolute;
  padding: 5px;
  border-radius: 4px;
  background-color: #2196F3;
  color: #ffffff; }
  .hover__price {
    font-weight: 700; }

#data-cointainer {
  width: 100%;
  display: flex;
  justify-content: center;
}

#left {
  margin-left: 48px;
}

#middle {
  padding: 0 48px;
  border-left: 1px solid #dae1e9;
  border-right: 1px solid #dae1e9;
}

#right {
  margin-right: 48px;
}

.box {
  width: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
}

.currentPrice .box:nth-child(1) {
  -webkit-animation: flash 1s;
          animation: flash 1s;
}

.heading {
  font-size: 2.5em;
  color: #2196F3;
}

.subtext {
  color: #cccccc;
}

@-webkit-keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}

@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}
